<template>
  <div class="config">
        <div>
            <p class="text">Min:</p>
            <input class="css-input" type="number" v-model="min" />
        </div>
        
        <div>
            <p class="text">Max:</p>
            <input class="css-input" type="number" v-model="max" />
        </div>

        <div>
            <p class="text">Text To Speech</p>
            <input type="checkbox" v-model="TTS" />
        </div>
        
        <button class="myButton" @click="save" >Guardar</button>
  </div>
</template>

<script>
export default {
    data() {
        return {
            min: 1,
            max: 99,
            TTS: true,
        }
    },
    methods: {
        save(){
            
            this.$store.state.start = true;
            this.$store.commit("setMax", this.max);
            this.$store.commit("setMin", this.min);
            this.$store.commit("setTTS", this.TTS);
            //this.$store.commit("setStart", true);
        }
    },
}
</script>

<style scoped>
.text{
    color: white;
}
.config{
    margin: auto;
    width: 300px;
    height: 400px;
    border-radius: 10px;
    background-color: rgb(62, 62, 62);
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: space-around;
    align-items: center;
}
</style>