<template>

    <div class="btn-row">
        <button class="myButton" @click="add()" >Siguente</button>
        <button class="myButton" @click="crear()" >Reiniciar</button>
        <button class="myButton" @click="conf()" >Configurar</button>
    </div>

    <div class="bingo-container">
        <div v-for="numero in bingoArray" key="numero.num" class="num-container" >
        
            <div v-if="numero.estado == 0" class="num no-suado">
                <p>{{ numero.num }}</p>
            </div>

            <div v-else-if="numero.estado == 1" class="num si-suado">
                <p>{{ numero.num }}</p>
            </div>

            <div v-else-if="numero.estado == 2" class="num actual">
                <p>{{ numero.num }}</p>
            </div>
         
        </div>
    </div>
</template>

<script>
import Speech from 'speak-tts'

export default {

    data() {
        return {
            max:this.$store.state.max,
            min:this.$store.state.min,
            bingoArray:[],
            bingoAleatorio:[],
            num:0,
            speech : new Speech()  
        }
    },
    mounted() {
        this.crear();
        
        // Speech.init({
        //     'volume': 1,
        //         'lang': 'es-ES',
        //         'rate': 1,
        //         'pitch': 1,
        //         'voice':'Google UK English Male',
        //         'splitSentences': true,
        //         'listeners': {
        //             'onvoiceschanged': (voices) => {
        //                 console.log("Event voiceschanged", voices)
        //             }
        //         }
        // })
    },
    methods: {
        add(){
            let variable = this.bingoAleatorio[this.num];
            
            for (let i = 0; i < this.bingoArray.length; i++) {
                if(this.bingoArray[i].num == variable.num){
                    
                    this.bingoArray[i].estado = 2; 
                }
                if(this.bingoArray[i].num != variable.num && this.bingoArray[i].estado == 2){
                    
                    this.bingoArray[i].estado = 1; 
                }
            }

            this.num ++;
            console.log(this.$store.state.TTS)
            if(this.speech.hasBrowserSupport() && this.$store.state.TTS) { // returns a boolean
                this.speech.setLanguage('es-ES');
                
                this.speech.speak({
                    text: 'El numero es '+variable.num,
                })
            }
            
        },

        crear(){
            this.bingoArray = [];
            for (let i = this.min; i <= this.max; i++) {
                this.bingoArray.push({num:i, estado:0});
            }
            this.bingoAleatorio = this.bingoArray.map(obj => ({...obj}));
            this.bingoAleatorio.sort((a, b) => 0.5 - Math.random());
        },
        conf(){
            this.crear();
            this.$store.state.start = false;
        }
    },
}
</script>

<style>
.num-container{
    width: 80px;
    height: 80px;
    background-color: white;
    border: solid 2px rgb(46, 46, 46);
    display: flex;
    align-items: center;
    justify-content: center;
}

.bingo-container{
    padding-top: 25px;
    padding-bottom: 25px;
    margin: auto;
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.num{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.no-suado{
background-color: white;
}

.si-suado{
background-color: rgb(39, 39, 39);
color: white;
}

.actual{
   background-color: red;
   color: white; 
}

.btn-row{
    margin: auto;
    padding: 25px;
    display: flex;
    flex-direction: row;
    gap: 25px;
    flex-wrap: wrap;
    align-content: space-between;
}
</style>