<template>
  <div class="container">
    <Config v-if="!startBingo"/>
    
    <Bingo v-else  />
  </div>
</template>

<script>
import Config from '@/components/Config.vue'
import Bingo from '@/components/Bingo.vue'
export default {
  components: {
    Config,
    Bingo 
  },
  data() {
    return {
      startBingo:false,
    }
  },

  watch:{
    '$store.state.start': function() {
      this.startBingo = this.$store.state.start;
    }
 
  }
}
</script>

