import { createStore } from 'vuex'

export default createStore({
  state: {
    min:0,
    max:100,
    start:false,
    TTS:true,

  },
  getters: {
  },
  mutations: {
    setStart: ( state, payload ) => {
      state.start = payload;
    },
    setMax: ( state, payload ) => {
      state.max = payload;
    },
    setMin: ( state, payload ) => {
      state.min = payload;
    },
    setTTS: ( state, payload ) => {
      state.TTS = payload;
    },
  },
  actions: {
  },
  modules: {
  }
})
